<template>
  <div class="cm-header-left" @click="goBack">
    <i class="el-icon-arrow-left"></i><span>{{$t('返回')}}</span>
  </div>
</template>

<script>
export default {
  computed: {},
  methods: {
    goBack () {
      this.$router.go(-1)
    }
  },
  data () {
    return {}
  },
  created () {

  },
  mounted () {

  }
}
</script>

<style>
  .cm-header-left {
    padding-right: 15px;
    margin-bottom: 3px;
    border-right: solid 1px var(--cor-grey);
  }
</style>



