<template>
  <el-container class="me">
    <Header :title="$t('Me')" class="cm-header">
    </Header>

    <el-main>
      <el-form
        ref="form"
        label-position="right"
        class="form"
        :model="form"
        size="medium"
        label-width="120px"
        :rules="checkRules">

        <div class="me-avatar-cont">
          <div class="me-avatar-parent">
            <!--<img class="me-avatar" :src="logoSrc" />
            <div class="me-avatar-upload">{{$t("Upload")}}</div>-->
            <img class="me-avatar" :src="avatar" alt/>
            <el-upload
              list-type="picture"
              action=''
              accept=".jpg, .png"
              :limit="1"
              :show-file-list="false"
              :auto-upload="false"
              :file-list="fileList"
              :on-change="getFile"
              class="me-avatar-upload"
              ref="upload"
              :before-upload="handleAvatarSuccess"
            >
              {{$t("Upload")}}

            </el-upload>
          </div>
        </div>

        <el-form-item prop="accountName" :label="$t('Name')">
          <el-input v-model="form.accountName" disabled suffix-icon="el-icon-user"></el-input>
        </el-form-item>
        <el-form-item prop="oldRawPassword" :label="$t('Password')">
          <el-input v-model="form.oldRawPassword" show-password></el-input>
        </el-form-item>
        <el-form-item prop="newRawPassword" :label="$t('NewPassword')">
          <el-input v-model="form.newRawPassword" show-password></el-input>
        </el-form-item>
        <el-form-item prop="mobile" :label="$t('Phone')">
          <el-input v-model="form.mobile"></el-input>
        </el-form-item>
        <el-form-item prop="email" :label="$t('Email')">
          <el-input v-model="form.email"></el-input>
        </el-form-item>
      </el-form>

    </el-main>
    <el-footer>
      <el-button class="me-btn" type="primary" @click="changeInfo">{{$t('ChangeInfo')}}</el-button>
    </el-footer>
  </el-container>
</template>

<script>
import {postApi} from '../../common/utils'
export default {
  components: {},
  computed: {},
  methods: {
    changeInfo () {
      const loading = this.$loading(this.$config.ls.loading)
      const data = this.form
      const currentDate = new Date()
      this.$post(this.$config.ls.bsPath + '/container/updateAccount', data).then((res) => {
        console.log('update', res)
        if (res) {
          this.$alert(this.$t('ChangeSuccessfully')).catch(()=>{});
          //get avatar
          this.$post(this.$config.ls.bsPath + '/clouddisk/download', {
            "fileName": res.body.avatar
          }).then((result) => {
            if (result) {
              console.log('get pic successfully',result);
              let avatarPic= 'data:image/png;base64,' + result.fileContent;
              const localDataStr=sessionStorage.getItem('loginUser')
              let localData=JSON.parse(localDataStr);

              const userDataNew =Object.assign(localData,{avatarPic},{avatar:res.body.avatar});
              //const userDataNew = {lastLogin: currentDate.toJSON(), mobile, accountId, email, auth: 'Bearer ' + currentToken,role,avatar,helloMsg,avatarPic}

              console.log("userDataNew",userDataNew);
              sessionStorage.setItem('loginUser', JSON.stringify(userDataNew))
              this.$router.go(0)
            }
          })
          //get avatar end

        }
      }).catch(e => {
        const error = JSON.parse(e)
        this.$alert(this.$t('UpdateFailed,') + error.messages[0].message).catch(() => {
        })
      }).finally(() => {
        loading.close()
      })
    },
    beforeAvatarUpload (file) {
      const isJPG = file.type === 'image/jpeg'
      const isLt2M = file.size / 1024 / 1024 < 2

      if (!isJPG) {
        this.$message.error('上传头像图片只能是 JPG 格式!')
      }
      if (!isLt2M) {
        this.$message.error('上传头像图片大小不能超过 2MB!')
      }
      return isJPG && isLt2M
    },
    getBase64 (file) {
      return new Promise(function (resolve, reject) {
        const reader = new FileReader()
        let imgResult = ''
        reader.readAsDataURL(file)
        reader.onload = function () {
          imgResult = reader.result
        }
        reader.onerror = function (error) {
          reject(error)
        }
        reader.onloadend = function () {
          resolve(imgResult)
        }
      })
    },
    getFile (file, fileList) {
      this.avatar = ''
      this.getBase64(file.raw).then(res => {
        const params = res.split(',')
        console.log({params})
        if (params.length > 0) {
          let fileType = params[0]
          this.fileType = fileType
          fileType = fileType.substr(11)
          const index = fileType.indexOf(';base64')
          fileType = fileType.substr(0, index)
          this.proofImage = params[1]
          const path=this.$config.ls.avatarPath;
          let picHeader=`data:image/${fileType};base64,`;
          this.form.avatarFileName = path+'userAvatar.'+fileType
          this.form.avatarFileContent = picHeader+params[1]
          this.form.avatarFileType = fileType
          this.avatar = this.form.avatarFileContent
        }
      }).catch((e) => {
        this.$alert(this.$t('UploadError') + e).catch(() => {
        })
      })
    },
    handleUploadRemove (file, fileList) {
      this.proofImage = ''
    },
    handlePictureCardPreview (file) {
      this.dialogImageUrl = file.url
      this.dialogVisible = true
    },
    handleAvatarSuccess (res, file) {
      // this.myData= file;
      console.log(res, file, this.$refs.upload)
      this.$refs.upload.clearFiles() // 上传成功之后清除历史记录
    }
  },
  data () {
    const localData = sessionStorage.getItem('loginUser')
    const avatarPath = this.$config.ls.avatarPath
    let avatarFileName = ''
    let userData = JSON.parse(localData)
    let {accountName, avatar,avatarPic, mobile, email, accountId} = userData
    avatarFileName = avatarPath + `avatar.jpeg`

    const checkPhone = (rule, value, callback) => {
      const phoneReg = /^1[3|4|5|7|8][0-9]{9}$/
      if (!value) {
        return callback(new Error('电话号码不能为空'))
      }
    }
    const checkEmail = (rule, value, callback) => {
      const mailReg = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/
      if (!value) {
        return callback(new Error('email canno be null'))
      }
    }

    return {
      form: {
        accountName: accountName,
        oldRawPassword: '',
        newRawPassword: '',
        email: email,
        mobile: mobile,
        avatarFileName: '',
        avatarFileType: '',
        avatarFileContent: ''
      },
      fileType: '',
      proofImage: '',
      dialogImageUrl: '',
      dialogVisible: false,
      imageUrl: '',
      fileList: [],
      avatar: avatarPic||'',
      checkRules: {
        accountName: [{ message: this.$t('Input_username'), trigger: 'blur'}],
        oldRawPassword: [{ message: this.$t('Input_password'), trigger: 'blur'}],
        newRawPassword: [{ message: this.$t('Input_confirmPassword'), trigger: 'blur'}],
        email: [{ message: this.$t('Input_email'), trigger: 'blur'}],
        mobile: [{ message: this.$t('Input_phone'), trigger: 'blur'}],
        token: [{ message: this.$t('Input_token'), trigger: 'blur'}]
      }
    }
  },
  created () {
  },
  mounted () {

  }
}
</script>

<style>
  .me {
  }

  .me-avatar-cont {
    display: flex;
    justify-content: center;
    margin-bottom: var(--size-md);
  }

  .me-avatar-parent {
    position: relative;
    width: 80px;
    height: 80px;
    border-radius: 50%;
    overflow: hidden;
    border: var(--border);
  }

  .me-avatar {
    width: 100%;
    height: 100%;
    object-fit: cover;

  }

  .me-avatar-upload {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 25px;
    color: var(--cor-white);
    background-color: rgba(0, 0, 0, 0.7);
    opacity: 0.7;
    z-index: 1000;
    font-size: var(--font-sm);
  }
  .me-btn{

  }
</style>



