<template>
  <el-container class="rg">
    <el-main class="rg-main">
      <el-form
        ref="form"
        label-position="left"
        label-width="15px"
        class="rg-form cm-flex"
        :model="form"
        size="medium"
        :rules="checkRules">
        <div class="rg-title">
          <div class="rg-title-left">W</div>
          <div class="rg-title-right">
            <div>OR</div>
            <div>LD</div>
          </div>
        </div>
        <el-form-item prop="accountName" label=" ">
          <el-input v-model="form.accountName" placeholder="用户名" suffix-icon="el-icon-user"></el-input>
        </el-form-item>
        <el-form-item prop="password" label=" ">
          <el-input v-model="form.password" placeholder="密码" show-password></el-input>
        </el-form-item>
        <el-form-item>
          <el-button-group class="btn-group">
            <el-button class="btn" type="primary" plain @click="$linkTo('/register')">注册</el-button>
            <el-button class="btn" type="primary" @click="login">登录</el-button>
          </el-button-group>
        </el-form-item>
      </el-form>
      <h3>用户登录</h3>
      <h5>CopyRight © 2021.07 SecWorld</h5>
    </el-main>
  </el-container>
</template>

<script>
  import logo from '../../assets/logo.png'
  import config from '../../../config'
  import {postRequest, saveLocalData} from '../../common/utils'
  import {AV} from '../../common/api';
  export default {
    components: {}, computed: {},
    methods: {
      back () {
        this.$router.push('/login')
      },
      checkEmail () {
        // https://gw.sit.ebaocloud.com/eBao/1.0/{name}/verify
//      postRequest(config.ls.containerHost + '/eBao/1.0/email_/verify', {
//        'text': this.form.email
//      }).then((res) => {
//        console.log('dddddd', res)
//      })
      },
      login () {
        this.$refs.form.validate((valid) => {
          if (valid) {
            const loading = this.$loading(config.common.loading)
            const data = this.form
            AV.User.logIn(data.accountName, data.password).then((res) => {
              if (res) {
                AV.User.current().getRoles().then((roles) => {
                  const rolesArr = AV.decodeData(roles)
                  saveLocalData('currentUserRoles', rolesArr);
                  this.$linkTo('/');
                });
              }
            }).catch(e => {
              this.$alert(this.$t('CommonApiError') + ',error' + e).catch(() => {
              })
              console.error('error of Home module', e)
            }).finally(() => {
              loading.close()
            })
          }
        })
      },
      sendSms () {
        const mobile = this.form.mobile
        if (!mobile) {
          this.$alert(this.$t('InvalidPhoneNumber')).catch(() => {
          })
          return
        }
        const mobileReg = /^[1][3,4,5,7,8][0-9]{9}$/;
        const mobileCheck = mobileReg.test((mobile))
        if (!mobileCheck) {
          this.$alert(this.$t('InvalidPhoneNumber')).catch(() => {
          })
        }
      }
    },
    data () {
      const checkEmail = (rule, value, callback) => {
        if (!value) {
          callback(new Error('请输入邮箱地址'));
        }
        const reg = /^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/;
        if (!reg.test(value)) {
          callback(new Error('邮箱格式不正确'));
        }
      };
      return {
        titleArr: ['Wo', 'rld'],
        form: {
          accountName: '',
          password: '',
          mobile: '',
          email: '',
          code: '',
          verifyType: 'sms'
        },
        sendButton: this.$t('Send'),
        sendButtonDisabled: false,
        checkRules: {
          accountName: [{required: true, message: "请输入用户名", trigger: 'blur'}],
          password: [{required: true, message: "请输入密码", trigger: 'blur'}],
          email: [{required: true, validator: checkEmail, trigger: 'blur'}],
          mobile: [{required: true, message: this.$t('Input_phone'), trigger: 'blur'}],
          code: [{required: true, message: '', trigger: 'blur'}]
        },
        logoSrc: logo
      }
    },
    created () {

    },
    mounted () {

    }
  }
</script>

<style scoped="">
  .rg-bg {

  }

  .rg {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    max-width: none;
    padding: 0;
    /*background-image: linear-gradient(to bottom right, #f9f7f7, #b3b1b1);*/
  }

  .rg::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    background: url(../../assets/registerBg.jpeg);
    background-size: 100% 100%;
    background-repeat: no-repeat;
    filter: blur(10px);
  }

  .rg-title {
    display: flex;
    justify-content: center;
    align-items: center;
    color: #716f6f;
    margin-bottom: var(--size-md);
    white-space: pre-line;
    font-size: 20px;
    font-weight: 900;
    z-index: 1;
  }

  .rg-title:first-letter {
    font-size: 36px;
  }

  .rg-title-left {
    font-size: 56px;
    margin-right: 3px;
  }

  .rg-title-right {

  }

  .rg-main{
    background-color: white;
  }

  @media (max-width: 768px) {
    .rg-main {
      height: 100%;
    }
  }

  @media (min-width: 769px) {
    .rg-main {
      height: 100%;
      max-height: 550px;
    }
  }

  .rg .el-main {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 0;
    max-width: 400px;
    width: 100%;
    z-index: 100;
  }

  .rg .rg-form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    box-sizing: border-box;
    padding: var(--size-md);
  }

  .rg-logo {
    max-width: 500px;
    width: 90%;
    height: auto;
  }

  .rg-logo-cont {
    max-width: 300px;
    height: auto;
  }

  .el-select {
    width: 100%;
  }

  .el-input {
    width: 100%;
  }

  .btn-group {
    display: flex;
  }

  .btn {
    flex: 1;
  }

  .rg-sms-btn {
    width: 70px;
  }
</style>



