import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/views/Home/index'
import Login from '@/views/Login/index'
import Register from '@/views/Register/index'
import ManageUser from '@/views/ManageUser/index'
import Error from '@/views/Error/index'
import Search from '@/views/Search/index'
import Me from '@/views/Me/index'
import ArticleList from '@/views/ArticleList/index'
import ArticleDetail from '@/views/ArticleDetail/index'
import ArticleAddOrEdit from '@/views/ArticleAddOrEdit/index'

Vue.use(Router)
const originalPush = Router.prototype.push

Router.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}
export default new Router({
  base: __dirname,
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/Login',
      name: 'Login',
      component: Login
    },
    {
      path: '/Register',
      name: 'Register',
      component: Register
    },
    {
      path: '/ManageUser',
      name: 'ManageUser',
      component: ManageUser
    },
    {
      path: '/Search',
      name: 'Search',
      component: Search
    },
    {
      path: '/Me',
      name: 'Me',
      component: Me
    },
    {
      path: '/ArticleList/:type',
      name: 'ArticleList',
      component: ArticleList
    },
    {
      path: '/ArticleDetail/:objectId',
      name: 'ArticleDetail',
      component: ArticleDetail
    },
    {
      path: '/ArticleAddOrEdit/:type/:objectId?',
      name: 'ArticleAddOrEdit',
      component: ArticleAddOrEdit
    },
    {
      path:'*',
      name: 'Error',
      component:Error
    }
  ]
})



