<template>
  <el-row :gutter="10">
    <el-col :xs="24" :span="8" class="ho-card-cont" v-for="(article, index) in articles" :key="index" :offset="0">
      <el-card class="ho-card" shadow="never" :body-style="{ padding: '5px'}">
        <img :src="article.icon?article.icon.url:defaultIcon" class="ho-image"
             @click="$linkTo(`/ArticleDetail/${article.objectId}`)"/>
        <div class="ho-info-cont">
          <main class="ho-title" @click="$linkTo(`/ArticleDetail/${article.objectId}`)">
            <h4>{{article.title}}</h4>
            <span
              class="cm-color-green cm-font-xs">{{getArticleContent(article.content)}}</span>
          </main>
          <footer>
            <el-button-group class="operate-button-cont">
              <el-button size="small" type="primary" icon="el-icon-chat-dot-square">{{$t('评论')}} 10</el-button>
              <el-button size="small" type="primary" plain icon="el-icon-star-off">{{$t('点赞')}} 30</el-button>
            </el-button-group>
          </footer>
        </div>
      </el-card>
    </el-col>
  </el-row>
</template>

<script>
  import {removeHtml} from '../../common/utils';
  import defaultIcon from '../../assets/logo.png';
  export default {
    props: ['articles'],
    computed: {

    },
    methods: {
      getArticleContent(content){
        content=removeHtml(content);
        if(content.length>35){
          content=content.substr(0,35)+'...';
        }
        return content;
      }
    },
    data () {
      return {
        defaultIcon
      }
    },
    created () {

    },
    mounted () {

    }
  }
</script>

<style>
  .ho-title {
    flex: 1;
    padding: 5px;
    font-size: 10px;
    text-align: left;
  }

  .ho-image {
    width: 80px;
    height: 80px;
    display: block;
    object-fit: cover;
    border:var(--border);
  }

  .ho-info-cont {
    flex: 1;
    display: flex;
    flex-direction: column;
    padding: var(--size-sm);
    margin-left: var(--size-xs);
    background-color: #f7f7f7;
  }

  .ho-card-cont {
    margin-bottom: var(--size-sm);
  }

  .ho-card > .el-card__body {
    display: flex !important;
    align-items: center !important;
  }
</style>



