<template>
  <el-container class="mg">
    <Header title="管理"/>
    <el-menu default-active="1" class="el-menu-demo" mode="horizontal">
      <el-menu-item index="1" @click="addRole">添加角色</el-menu-item>
      <el-menu-item index="2" @click="createActivationCode">生成激活码</el-menu-item>
    </el-menu>
    <el-main>123</el-main>
    <el-footer></el-footer>
  </el-container>
</template>

<script>
  import {AV} from '../../common/api';
  import {uuid} from '../../common/utils';
  export default {
    components: {}, computed: {},
    methods: {
      addRole(){
        const roleAcl = new AV.ACL();
        roleAcl.setPublicReadAccess(true);
        roleAcl.setWriteAccess(AV.User.current(), true);
        const admin = new AV.Role('admin', roleAcl);
        admin.getUsers().add(AV.User.current());
        admin.save().then((res) => {
          console.log({res});
        });
      },
      createActivationCode(){
        this.$prompt('请输入被邀请者邮箱').then(res => {
          let email = res.value;
          if (email) {
            const reg = /^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/;
            if (!reg.test(email)) {
              this.$alert('邮箱格式不正确').catch(() => {
              });
              return;
            }
            const code = uuid();
            AV.addData('activationCode', {email, code}).then(() => {
              this.$alert(`激活码是:${code}`).catch(() => {
              });
            }).catch(e=>{
              this.$alert(`无法创建激活码:${e}`).catch(() => {
              });
            })
          }
        }).catch(() => {
        });
      }
    },
    data () {
      return {
        titleArr: ['Wo', 'rld'],

      }
    },
    created () {

    },
    mounted () {

    }
  }
</script>

<style scoped="">

</style>



