import axios from 'axios'
import config from '../../config'
function getApi(url, headers = {}) {
  let header = {}
  if (headers) {
    header = Object.assign({}, headers, {'X-ebao-tenant-id': config.ls.tenant}, getAuthorization())
  } else {
    header = Object.assign({}, {'X-ebao-tenant-id': config.ls.tenant}, getAuthorization())
  }
  return axios.get(url, {headers: header})
    .then((res) => res.data)
    .then((response) => {
      if (response.result === 1) {
        return response
      } else {
        throw new Error('error when get api,response:' + JSON.stringify(response))
      }
    }).catch(e => {
      throw new Error('error when get api,' + e)
    })
}
