export function getLoginUser() {
  const localDataStr = sessionStorage.getItem('loginUser');
  let localData = {};
  if (localDataStr) {
    localData = JSON.parse(localDataStr);
  }
  return localData;
}

export function saveLoginUser(valueObject) {
  let localData = getLoginUser();
  Object.assign(localData, valueObject);
  sessionStorage.setItem('loginUser', JSON.stringify(localData));
}

export function getLocalData(key) {
  const localDataStr = localStorage.getItem(key);
  let localData = null;
  if (localDataStr) {
    localData = JSON.parse(localDataStr);
  }
  return localData;
}

export function saveCacheData(key, valueObject) {
  let localData = getLocalData(key);
  let data = valueObject;
  if (localData && typeof localData === 'object') {
    data = Object.assign(localData, valueObject);
  }
  sessionStorage.setItem(key, JSON.stringify(data));
}

