function linkTo(path) {
  this.$router.push(path)
}
function mergeMessages(lang) {
  let views
  let components
  if (lang === 'zh') {
    views = require.context('../views', true, /zh.js$/i)
    components = require.context('../components', true, /zh.js$/i)
  } else {
    views = require.context('../views', true, /en.js$/i)
    components = require.context('../components', true, /en.js$/i)
  }
  let messages = {}
  views.keys().forEach(key => {
    messages = Object.assign(messages, views(key).default)
  })
  components.keys().forEach(key => {
    messages = Object.assign(messages, components(key).default)
  })
  return messages
}
function checkLogin() {
  const userData = sessionStorage.getItem('loginUser')
  const loginData = JSON.parse(userData)
  let {loginExpireSeconds} = config.common
  let checkLogin = false
  if (loginData) {
    const {lastLogin} = loginData
    const loginDate = new Date(lastLogin).getTime()
    const now = new Date().getTime()
    const diff = now - loginDate
    loginExpireSeconds = loginExpireSeconds * 1000
    if (diff <= loginExpireSeconds) {
      checkLogin = true
    }
  }
  return checkLogin
}
export function setLanguage() {
  console.log('setLan')
  let currentLang = localStorage.getItem('pocLang')
  let nextLang
  if (currentLang === 'zh') {
    nextLang = 'en'
  } else {
    nextLang = 'zh'
  }
  localStorage.setItem('pocLang', nextLang)
  this.$i18n.locale = nextLang
}

export function filterObject(data, {key, value}, returnKey) {
  let returnData = null;
  for (let item of data) {
    if (item[key] === value) {
      returnData = item[returnKey];
    }
  }
  return returnData;
}

export function formatDate(date, showTime = true) {
  if (!date) {
    return '';
  }
  let newDate = date;
  if (!showTime) {
    const index = newDate.lastIndexOf('T');
    return newDate.substr(0, index);
  } else {
    newDate = newDate.replace('T', ' ');
    const index = newDate.lastIndexOf('.');
    return newDate.substr(0, index);
  }
}

export function removeHtml(html) {
  return html.replace(/<[^>]+>/g, '');
}

export function uuid() {
  const s = [];
  const hexDigits = "0123456789abcdef";
  for (let i = 0; i < 36; i++) {
    s[i] = hexDigits.substr(Math.floor(Math.random() * 0x10), 1);
  }
  s[14] = "4"; // bits 12-15 of the time_hi_and_version field to 0010
  s[19] = hexDigits.substr((s[19] & 0x3) | 0x8, 1); // bits 6-7 of the clock_seq_hi_and_reserved to 01
  s[8] = s[13] = s[18] = s[23] = "-";
  return s.join("");
}

export function message(text,type=3){
  let str='success';
  if(type===2){
    str='info';
  }else if(type===3){
    str='warning';
  }else if(type===4){
    str='danger';
  }
  this.$message({type:str,message:text});
}
export {getApi, postApi, mergeMessages, checkLogin, linkTo}



