<template>
  <div id="app">
    <router-view class="router-view" :key="key"/>
  </div>
</template>

<script>
  export default {
    name: 'App',
    computed: {
      key(){
        return this.$route.name + new Date()
      }
    },
    watch: {},
    data () {
      return {}
    },
    methods: {}
  }
</script>

<style>
  *{
    box-sizing: border-box;
  }
  html, body, #app {
    height: 100%;
    margin: 0;
    box-sizing: border-box;
  }

  button {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
  }

  ul {
    list-style: none;
    padding-left: 0;
    margin: 0;
  }

  h3, h4 {
    margin: 0;
  }

  #app {
    /*max-width: 1000px;*/
    font-family: Constantia, 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    display: flex;
    margin: 0 auto;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .router-view {

  }

  @media (max-width: 1140px) {
    #app {

    }
  }

  .main {
    display: flex;
    justify-content: center;
    width: 800px;
    height: 600px;
    border: solid 1px #bbb;
  }

  ::-webkit-scrollbar {
    width: 5px;
    height: 3px;
  }

  ::-webkit-scrollbar-button {
    height: 5px;
    background: #ddd;
  }

  ::-webkit-scrollbar-track-piece {
    background: #fff;
  }

  ::-webkit-scrollbar-thumb {
    background: #646464;
  }

  .el-container {
    width: 100%;
    height: 100%;
    padding: 5px;
    max-width: 1000px;
    flex-direction: column !important;
  }
  .el-footer{
    display: flex;
    height: 40px!important;
    padding:0 0 !important;
    margin-top: 5px;
  }
  .el-footer .el-button{
    flex: 1;
    border-radius: 0;
  }
  .el-main {
    padding: var(--size-sm) !important;
  }

  .el-message-box__wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .el-message-box {
    width: 70%;
    min-width: 200px;
    max-width: 300px;
  }
  .el-message {
    min-width: 300px !important;
    z-index: 3000 !important;
  }
  .cm-vertical{
    display: flex !important;
    flex-direction: column;
  }
  .cm-horizon{
    display: flex !important;
    flex-direction:row;
  }
  .cm-padding-v{
    padding:var(--size-sm) 0 !important;
  }
  .cm-padding-h{
    padding:0 var(--size-sm) !important;
  }
  .cm-padding{
    padding:var(--size-sm);
  }
  .cm-center{
    justify-content: center;
    align-items: center;
  }
  .cm-full{
    width: 100%;
    height: 100%;
  }
  .cm-full-h{
    height: 100%;
  }
  .cm-full-w{
    width: 100%;
  }
  .cm-flex{
    flex: 1;
  }
  .cm-overflow-y{
    overflow-y: scroll;
  }
  /*----FONT----*/
  .cm-font-xs{
    font-size: var(--font-xs);
  }
  .cm-font-sm{
    font-size: var(--font-sm);
  }
  .cm-font-md{
    font-size: var(--font-md);
  }
  .cm-font-lg{
    font-size: var(--font-lg);
  }
  .cm-color-green{
    color:var(--cor-green);
  }
  .cm-color-grey{
    color:var(--cor-grey);
  }
  .cm-border-b{
    border-bottom: var(--border);
  }
  .cm-bg-grey{
    background-color: var(--cor-grey);
  }
  .cm-bg-white{
    background-color: var(--cor-white);
  }
  .cm-align-l{
    text-align: left;
  }
  .cm-align-c{
    text-align: center;
  }
  .cm-radius-n{
    border-radius: 0;
  }
</style>



