<template>
  <el-container class="ho">
    <Header :showBack="false" title="首页">
      <div @click="linkTo('/search')">
        <i class="el-icon-search ho-search"></i>
        <span class="hidden-xs-only ho-search-text">{{$t('Search')}}</span>
      </div>
    </Header>
    <main class="ho-main">
      <el-carousel :autoplay="false" type="card" trigger="click" height="150px">
        <el-carousel-item v-for="item in 3" :key="item">
          <!--<img :src="hot[item-1]" class="ho-hot-img"/>{{item}}-->
          <div class="ho-notice cm-full">
            <img :src="hot[item-1]" class="ho-hot-img"/>
            <div class="ho-notice-text cm-vertical">欢迎来到第二世界</div>
          </div>
        </el-carousel-item>
      </el-carousel>
      <el-tabs tab-position="left" v-model="currentCate" @tab-click="changeTab">
        <el-tab-pane label="最新" name="0">
          <Article :articles="list[0]"/>
        </el-tab-pane>
        <el-tab-pane label="设定" name="1">
          <Article :articles="list[1]"/>
        </el-tab-pane>
        <el-tab-pane label="正章" name="2">
          <Article :articles="list[2]"/>
        </el-tab-pane>
        <el-tab-pane label="角色" name="3">
          <Article :articles="list[3]"/>
        </el-tab-pane>
        <el-tab-pane label="术式" name="4">
          <Article :articles="list[4]"/>
        </el-tab-pane>
      </el-tabs>
      <!--<el-backtop target=".ho-main" :visibility-height="0"></el-backtop>-->
      <el-footer v-if="admin">
        <el-button type="success" @click="$linkTo('/ArticleAddOrEdit/1')">写文章</el-button>
      </el-footer>
    </main>
  </el-container>
</template>

<script>
  import config from '../../../config/index'
  import icon2 from '../../assets/article/icon-0.jpeg'
  import icon1 from '../../assets/article/icon-1.jpeg'
  import icon0 from '../../assets/article/icon-2.jpeg'

  import hot0 from '../../assets/article/hot-0.jpeg'
  import hot1 from '../../assets/article/hot-1.jpeg'
  import hot2 from '../../assets/article/hot-2.jpeg'

  import Article from './article.vue'
  import {saveLoginUser, getCacheData, saveCacheData} from '../../common/utils';
  import {AV} from '../../common/api';
  export default {
    components: {
      Article
    },
    computed: {},
    methods: {
      isAdmin(){
        AV.isAdmin().then((is) => {
          this.admin = is;
        })
      },
      linkTo (path) {
        this.$router.push(path)
      },
      changeTab () {
        this.getList(this.currentCate)
      },
      getList(type){
        const articleType = Number(type);
        let condition = {
          operator: '==',
          data: {type: articleType},
          limit: 20,
          order: {type: 'desc', field: 'updatedAt'}
        };
        if (articleType === 0) {
          condition = {order: {type: 'desc', field: 'updatedAt'}, limit: 20};
        }
        const tableName = 'articleList';
        const loading = this.$loading(this.$loadingConfig)
        AV.queryList(tableName, condition).then((res) => {
          this.list[articleType] = res;
        }).catch(e => {
          console.error('error of Home module', e)
        }).finally(() => {
          loading.close()
        })
      }
    },
    data () {
      return {
        isCollapse: true,
        currentDate: new Date(),
        list: {0:[],1:[],2:[],3:[],4:[]},
        currentCate: '0',
        icons: [icon0, icon1, icon2],
        hot: [hot0, hot1, hot2],
        admin:false,
      }
    },
    created () {
    },
    mounted () {
      this.getList('0');
      this.isAdmin();
    }
  }
</script>

<style>
  .ho {
    width: 100%;
  }
  .ho-notice{
    position: relative;
  }
  .ho-notice-text{
    position: absolute;
    bottom:0;
    left:0;
    width: 100%;
    height: 30px;
    background-color: white;
    opacity: 0.7;
    font-size: var(--font-sm);
    justify-content: flex-end;
  }
  .ho-hot-img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  .ho-search {
  }

  .ho-search-text {
    font-size: var(--font-sm);
    color: #303133;
  }

  .ho-main {
    display: flex;
    flex-direction: column;
    flex: 1;
    width: 100%;
    padding: 5px;
    overflow: hidden;
  }

  .el-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 150px;
    margin: 0;
  }

  .el-carousel__item:nth-child(2n) {
    background-color: white;
  }

  .el-carousel__item:nth-child(2n+1) {
    background-color: white;
  }

  .operate-button-cont {
    display: flex !important;
    width: 100%;
  }

  .operate-button-cont > .el-button {
    flex: 1;
    padding: 5px;
  }

  .ho .el-tabs {
    flex: 1;
  }

  .ho .el-tabs__item {
    padding: 0 var(--size-sm) 0 0;
  }

  .ho .el-tabs__content, .ho .el-tab-pane, .ho .el-row {
    height: 100%;
    /*background-color: var(--cor-grey);*/
  }

  .ho .el-row {
    overflow-y: scroll;
  }
</style>



