<template>
  <el-container class="se">
    <Header title="" class="cm-header">
      <div class="se-input">
        <el-input :placeholder="$t('文章标题')" v-model="keyword">
          <el-button slot="append" @click="search(keyword)" icon="el-icon-search"></el-button>
        </el-input>
      </div>
    </Header>
    <aside class="se-keywords">
      <h4>{{$t('SearchKeywords')}}:</h4>
      <div class="se-tag-cont">
        <el-tag type="success" effect="dark" @click="search('M')">{{$t('M')}}</el-tag>
        <el-tag type="success" @click="search('火球术')">{{$t('火球术')}}</el-tag>
        <el-tag type="success" @click="search('洛希维')">{{$t('洛希维')}}</el-tag>
      </div>
    </aside>

    <el-main>
      <h4 class="se-results-label">{{$t("SearchResults")}}:</h4>
      <ul v-if="results.length>0">
        <li @click="$linkTo(`/ArticleDetail/${result.objectId}`)" class="se-result cm-font-xs" v-for="(result, index) in results" :key="index">
          <el-card class="se-card">
            <img class="se-icon" :src="resultIcon" width="60px" height="60px" />
            <main class="se-result-main">
              <h4>{{result.title}}</h4>
              <span class="cm-color-green">{{getArticleContent(result.content)}}</span>
            </main>
          </el-card>
        </li>
      </ul>
      <ul v-else>
        <li class="se-result">
          搜索结果为空
        </li>
      </ul>
    </el-main>
  </el-container>
</template>

<script>
import {AV} from '../../common/api'
import {removeHtml} from '../../common/utils';
import defaultIcon from '../../assets/logo.png';
export default {
  components: {
  },
  computed: {},
  methods: {
    search (keyword) {
      this.keyword = keyword
      if(!keyword){
          this.$message({message:'请输入搜索关键字',type:'warning'});
          return;
      }
      const query = new AV.Query('articleList');
      const loading=this.$loading(this.$loadingConfig);
      query.contains('title',keyword);
      query.find().then(res => {
        this.results = AV.decodeData(res);
      }).catch(e => {
        this.$alert('error:' + e);
      }).finally(()=>{
        loading.close()
      });
    },
    getArticleContent(content){
      content=removeHtml(content);
      if(content.length>35){
        content=content.substr(0,35)+'...';
      }
      return content;
    }
  },
  data () {
    return {
      resultIcon:defaultIcon,
      keyword: '',
      results: []
    }
  },
  created () {
  },
  mounted () {

  }
}
</script>

<style>
  .se-input{
    flex: 1;
    width: auto;
    margin-left: var(--size-md);
  }
  .se .el-main{
    flex:1;
    overflow-y: scroll;
    padding:0;
  }
  .se-input .el-input__inner{
    height: 30px;
    line-height: 30px;
  }
  .se .hd-title{
    flex: 0;
    margin-right: 0;
  }
  .se-input  .el-button{
    padding:5px;
  }
  .se-icon{
    width: 60px;
    height: 60px;
    border: var(--border);
    padding:var(--size-xs);
    margin-right: var(--size-sm);
  }
  .se-result{
    margin-bottom: var(--size-sm);
  }
  .se .el-card__body{
    display: flex;
    padding: var(--size-sm);
  }
  .se-keywords{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: var(--size-sm);
    border-bottom: var(--border);
  }
  .se-result-main{
    text-align: left;
  }
  .se-tag-cont{
    padding: var(--size-sm) 0 0 0;
  }
  .se-results-label{
    text-align: left;
    margin-bottom:var(--size-sm);
  }
  .se-card >.el-card__body{
    align-items: center;
  }
</style>



