<template>
  <el-header class="hd">
    <div class="cm-hd-back">
      <Back v-if="showBack"/>
    </div>
    <h4 class="hd-title">{{title}}</h4>
    <slot></slot>
    <el-menu
      default-active="2"
      class="hidden-xs-only"
      mode="horizontal">
      <el-menu-item @click="linkTo('/')" index="1">
        <i class="el-icon-house"></i>
        <span slot="title">{{$t('Home')}}</span>
      </el-menu-item>
      <el-submenu v-for="menu in menus" :index="menu.index" v-if="menu.sub">
        <template slot="title">
          <i :class="menu.icon"></i>
          <span>{{menu.name}}</span>
        </template>
        <el-menu-item v-for="sub in menu.sub" :index="sub.index" @click="sub.link?linkTo(sub.link):sub.click()">
          {{sub.name}}


        </el-menu-item>
      </el-submenu>
      <el-menu-item
        v-for="menu in menus"
        :index="menu.index"
        v-if="!menu.sub"
        @click="menu.link?linkTo(menu.link):menu.click()">
        <template slot="title">
          <i :class="menu.icon"></i>
          <span class="hd-user-name">{{menu.name}}</span>
        </template>
      </el-menu-item>

      <el-menu-item index="0" class="hd-avatar-cont" @click="$linkTo('/me')">
        <el-avatar size="large" :src="avatar" class="hd-avatar"></el-avatar>
      </el-menu-item>
    </el-menu>
    <div :class="menuClass" ref="menuIcon" @click="menuIconClick">
      <i class="el-icon-menu hd-menu-icon"></i>
    </div>
    <el-drawer
      :visible.sync="showMenu"
      :show-close="false"
      :append-to-body="true"
      :withHeader="false"
      size="200px"
      direction="rtl">
      <el-menu
        default-active="2"
        class="hd-drawer-menu">
        <el-menu-item index="0" class="hd-avatar-cont" @click="$linkTo('/me')">
          <el-avatar size="large" :src="avatar" class="hd-avatar" icon="el-icon-user-solid"></el-avatar>
          <span class="hd-user-name">{{userName}}</span>
        </el-menu-item>

        <el-menu-item @click="linkTo('/')" index="1">
          <i class="el-icon-house"></i>
          <span slot="title">{{$t('Home')}}</span>
        </el-menu-item>
        <el-submenu class="hd-sub-menu" v-for="menu in menus" v-if="menu.sub" :index="menu.index">
          <template slot="title">
            <i :class="menu.icon"></i>
            <span>{{menu.name}}</span>
          </template>
          <el-menu-item v-for="sub in menu.sub" :index="sub.index" @click="sub.link?linkTo(sub.link):sub.click()">
            {{sub.name}}


          </el-menu-item>
        </el-submenu>
        <el-menu-item
          v-for="menu in menus"
          :index="menu.index"
          v-if="!menu.sub"
          @click="menu.link?linkTo(menu.link):menu.click()">
          <i :class="menu.icon"></i>
          <span slot="title">{{menu.name}}</span>
        </el-menu-item>
      </el-menu>
    </el-drawer>
  </el-header>
</template>

<script>
  import Back from '../Back/index.vue'
  import {setLanguage, getRequest, getLocalData} from '../../common/utils'
  import config from '../../../config'
  import {AV} from '../../common/api';
  export default {
    props: {
      title: {type: String, required: true},
      showBack: {
        type: Boolean,
        default: true,
      }
    },
    components: {Back},
    computed: {
      menus () {
        let menus = null;
        const roles = getLocalData('currentUserRoles');
        let roleName = '';
        for (let role of roles) {
          if (role.name === 'admin') {
            roleName = "admin";
            break;
          }
        }
        menus = [
          {
            index: '2',
            icon: 'el-icon-document',
            name: this.$t('文章'),
            sub: [
              {index: '2-1', name: this.$t('设定'), link: '/ArticleList/1'},
              {index: '2-2', name: this.$t('正章'), link: '/ArticleList/2'},
              {index: '2-3', name: this.$t('角色'), link: '/ArticleList/3'},
              {index: '2-4', name: this.$t('术式'), link: '/ArticleList/4'}
            ]
          },
          {index: '3', icon: 'el-icon-chat-dot-round', name: '消息', click: this.setLang},
          //{index: '4', icon: 'el-icon-brush', name: this.$t('ClearCache'), click: this.clearCache},
        ];
        if (roleName === 'admin') {
          menus.push({
            index: '4',
            icon: 'el-icon-user',
            name: this.$t('管理'),
            sub: [
              {index: '4-1', name: this.$t('用户管理'), link: '/ManageUser'},
              {index: '4-2', name: this.$t('文章管理'), click: this.manageArticle.bind(this)},
              {index: '4-3', name: this.$t('公告管理'), link: '/ManageNotice'}
            ],
            link: '/management'
          }, {
            index: '5',
            icon: 'el-icon-switch-button',
            name: this.$t('LogOut'),
            click: this.logOut
          });
        } else {
          menus.push({
            index: '5',
            icon: 'el-icon-switch-button',
            name: this.$t('LogOut'),
            click: this.logOut
          });
        }
        return menus;
      },
      subMenu () {
        return {}
      },
      userName () {
        let currentUserStr = AV.User.current();
        let userName = ''
        if (currentUserStr) {
          let currentUser = JSON.parse(JSON.stringify(currentUserStr));
          userName = currentUser.username
        }
        return userName
      }
    },
    methods: {
      linkTo (link) {
        this.$router.push(link)
      },
      logOut () {
        AV.User.logOut();
        localStorage.clear();
        sessionStorage.clear();
        this.$linkTo('/login')
      },
      clearCache () {
        sessionStorage.clear()
      },
      setLang () {
        let currentLang = localStorage.getItem('pocLang')
        let nextLang
        if (currentLang === 'zh') {
          nextLang = 'en'
        } else {
          nextLang = 'zh'
        }
        localStorage.setItem('pocLang', nextLang)
        this.$i18n.locale = nextLang
      },
      menuIconClick () {
        this.showMenu = true
      },
      manageArticle(){
        this.$alert('管理员账号登录后，在各分类的文章列表页面编辑管理').catch(() => {
        });
      }
    },
    data () {
      let currentUserStr = AV.User.current();
      let avatar = ''
      if (currentUserStr) {
        let currentUser = AV.decodeData(currentUserStr);
        avatar = currentUser.avatar?currentUser.avatar.url:'';
      }
      return {
        showMenu: false,
        avatar,
        menuClass: 'hidden-sm-and-up hd-menu-cont'
      }
    },
    created () {

    },
    mounted () {
    }
  }
</script>

<style>
  .hd {
    display: flex;
    align-items: center;
    width: 100%;
    padding-left: 0;
    padding-right: 0;
    height: 40px !important;
    border-bottom: var(--border);
    z-index: 2;
    background-color: var(--cor-white);
  }

  .cm-hd-back {
    width: 70px;
  }

  .hd-title {
    flex: 1;
  }

  .hd-user-name {
    line-height: 3;
  }

  .hd-menu-cont {
    width: 60px;
  }

  .hd-menu-icon {
    font-size: 18px;
  }

  .hd-drawer-menu .el-submenu__title {
    padding-left: 0 !important;
  }

  .hd-avatar-cont {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: auto;
    text-align: center;
  }

  .el-menu.el-menu--horizontal {
    /*border-bottom: none;*/
  }

  .el-menu-item:hover {
    background-color: transparent !important;
  }

  .hd-drawer-menu {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: left;
    margin-top: 30px;
    border-right: 0 !important;
  }

  .hd-sub-menu {
    padding-left: 20px;
  }

  .hd-drawer-menu .el-menu-item, .hd-drawer-menu .el-submenu {
    width: 100%;
    text-align: center;
    box-sizing: border-box;
    border-bottom: var(--border);
  }

  .el-menu-item:last-child {
    border-bottom: none;
  }

  .el-submenu .el-menu-item {
    min-width: auto;
  }

  .hd .el-submenu .el-menu-item {
    padding-left: 30px !important;
    padding-right: 0 !important;
    border-bottom: 0;
    min-width: 0;
  }

  .hd .el-menu-item, .hd .el-submenu__title {
    height: 40px !important;
    line-height: 40px !important;
  }

  .hd-avatar {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 35px;
    height: 35px;
    margin-bottom: 3px;
  }

  .hd-avatar > [class^=el-icon-] {
    margin-right: 0;
  }

  .hd-icon-rotate {
    transform: rotate(180deg);
    transition: ease 1s;
  }
</style>



