// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import 'babel-polyfill'
import ElementUI from 'element-ui'
import locale from 'element-ui/lib/locale/lang/zh-CN'
import 'element-ui/lib/theme-chalk/index.css'
import 'element-ui/lib/theme-chalk/display.css'
import i18n from './i18n'
import './styles/variables.css'
import {linkTo,message} from './common/utils'
import {AV} from './common/api';
import config from '../config/'
import Header from './components/Header/index.vue'
import Footer from './components/Footer/index.vue'

Vue.use(ElementUI, {locale})
// Vue.use(utils);
Vue.config.productionTip = false
Vue.component('Header', Header)
Vue.component('Footer', Footer)
router.beforeEach((to, from, next) => {
  const currentUser = AV.User.current();
  if (to.name !== 'Login' && to.name !== 'Register' && !currentUser) {
    next('/Login')
  } else if (to.name === 'ManageUser'||to.name === 'ArticleAddOrEdit') {
    AV.isAdmin().then((isAdmin) => {
      if (isAdmin) {
        next();
      } else {
        next('/Error');
      }
    })
  } else {
    next()
  }
})
Vue.prototype.$linkTo = linkTo
Vue.prototype.$log = console.log
Vue.prototype.$loadingConfig = config.common.loading
Vue.prototype.$config = config
Vue.prototype.$msg=message;

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,

  components: {
    App
  },

  i18n,
  template: '<App/>'
})



